import React, { Component } from "react";

class TicTacToe extends Component {
  render() {
    return (
      <section className="resume-section p-3 p-lg-5 d-flex d-column">
        <h3>Tic-Tac-Toe</h3>
        <div class="my-auto" />
      </section>
    );
  }
}

export default TicTacToe;



